export default {
    name: 'switchState',
    data () {
        return {
            state: 'online'
        }
    },
    template: `
        <div class="switch">
            <input id="swOnline"type="radio" name="typeSwitch" checked="checked"/>
            <label 
              class="switch-state" 
              for="swOnline"
              v-on:click="state = 'online'">
              <span>Онлайн</span>
            </label>
            
            <input id="swLocal" type="radio" name="typeSwitch"/>
            <label 
              class="switch-state" 
              for="swLocal"
              v-on:click="state = 'offline'">
              <span>Локально</span>
            </label>
      </div>
    `,
    watch: {
        state: function(){
            this.$emit('switchState', this.state)
        }
    }
}


